___CSS_LOADER_EXPORT___.push([module.id, `.project-options {
    border: none;
    cursor: pointer;
    position: relative;
    display: inline;
}

.options-menu {
    display: inline;
    position: absolute;
    background-color: #f1f1f1;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
    width: fit-content;
}

.options-menu button {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    width: 100%;
    text-align: left;
}

.modal {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 300px;
    /* Assign a value */
    height: 200px;
    /* Assign a value */
    margin: auto;
    padding: 20px;
    box-sizing: content-box;
    border-radius: 5px;
}

.remove-modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    gap: 20px;
    width: 300px;
    height: fit-content;
}

.remove-modal .form-buttons {
    display: flex;
    justify-content: center;
}

.remove-modal .form-buttons button {
    padding: 10px 15px;
    background-color: beige;
    border-radius: 5px;
    font-weight: 600;
}

.remove-modal .form-buttons button#submit-project {
    background-color: #c4001f;
    color: white;
}

.modal::backdrop {
    background: rgb(0 0 0 / 25%);
}

#submit-removal {
    background-color: #c4001f;
}

input#project-name {
    border: 1px solid rgba(128, 128, 128, 0.356);
    padding: 5px;
    border-radius: 5px;
}

.project-switch {
    display: flex;
    gap: 5px;
}
